<template>
  <div id="app">
    <h1>Snake with Vue.js</h1>
    <snake v-bind:is-playing="isPlaying" @isPlaying="updateState"></snake>
    <div id="game-menu">
      <button id="button" v-on:click="isPlaying ? stopGame() : startGame()">{{isPlaying ? "Stop" : "Start"}}</button>
    </div>
    <audio loop><source src="../src/assets/Funky Chill loop.wav"></audio>
  </div>
</template>

<script>
import Snake from './components/Snake.vue'

export default {
  name: 'App',
  components: {
    Snake
  },
  data() {
    return {
      isPlaying: false,
      points: 0
    }
  },
  methods: {
    startGame() {
      this.isPlaying = true;
      this.playSound();
    },
    stopGame() {
      this.isPlaying = false;
      this.stopSound();
    },
    updateState(bool) {
      this.isPlaying = bool;
      if(!bool) {
        this.stopSound();
      }
    },
    playSound() {
      const audioPromise = document.querySelector('audio').play();
      if (audioPromise !== undefined) {
        audioPromise.then(function() {
          // Automatic playback started!
        }).catch(function(error) {
          // Automatic playback failed.
          // Show a UI element to let the user manually start playback.
          console.log(error);
        });
      }
    },
    stopSound() {
      const audioPromise = document.querySelector('audio').pause();
      if (audioPromise !== undefined) {
        audioPromise.then(function() {
          // Automatic playback started!
        }).catch(function(error) {
          // Automatic playback failed.
          // Show a UI element to let the user manually start playback.
          console.log(error);
        });
      }
    }
  }
}
</script>

<style>
  #app {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
  }

  button {

  }
</style>
